export default [{"headline":"12345678910 London! house prices: Alarm on home loans as prices surge","diptext":"The Bank of England today raised the alarm over homebuyers taking on bigger mortgages as house prices continue to soar in London.","author":"Nicholas Cecil & Jonathan Prynn","source":"Evening Standard","date":"02/07/2015","url":"http://www.standard.co.uk/news/london/alarm-on-home-loans-as-prices-surge-10361137.html"},{"headline":"Politicians call to stem the 'shameful' exodus of the poor from London","diptext":"Politicians and campaigners are calling for a radical overhaul of housing policy in London after The Independent revealed that 50,000 homeless families have been pushed out of boroughs.","author":"Daniel Douglas","source":"Evening Standard","date":"30/04/2015","url":"http://www.independent.co.uk/news/uk/home-news/politicians-and-campaigners-call-to-stem-the-shameful-exodus-of-the-poor-from-london-10217183.html"},{"headline":"What the housing crisis means for you","diptext":"The housing crisis is moving up the political agenda. It’s not only hurting first-time buyers: it’s affecting all of us, from families squeezed into flats to millionaires despairing about taxes.","author":"Martina Lees","source":"The Sunday Times","date":"19/04/2015","url":"http://www.thesundaytimes.co.uk/sto/style/homes_and_gardens/Move/article1544885.ece"},{"headline":"London: the city that ate itself","diptext":"London is a city ruled by money. The things that make it special – the markets, pubs, high streets and communities – are becoming unrecognisable. The city is suffering a form of entropy whereby anything distinctive is converted into property value. Can the capital save itself?","author":"Rowan Moore","source":"The Guardian","date":"28/06/2015","url":"http://www.theguardian.com/uk-news/2015/jun/28/london-the-city-that-ate-itself-rowan-moore"},{"headline":"Goodbye London: why people are leaving the capital","diptext":"London-born Rafael Behr has left the capital, and he’s not the only one. As prices spiral, many are seeking a gentler life elsewhere. Here he explores his motives and how he sees the city changing and meets three more residents who’ve gone.","author":"Rafael Behr","source":"The Observer","date":"29/06/2015","url":"http://www.theguardian.com/uk-news/2015/jun/29/goodbye-london-moving-to-brighton-house-prices"},{"headline":"London housing crisis creates canal squeeze","diptext":"A rise in the number of people choosing to live on canals in London is starting to put a strain on London's waterways, experts have said.","author":"Tarah Welsh","source":"BBC","date":"05/05/2015","url":"http://www.bbc.co.uk/news/uk-england-london-32593927"},{"headline":"Britain’s housing crisis is a human disaster","diptext":"Rising house prices have been willed by public policy over decades. The fallout for families, communities and business has been severe.","author":"Rowan Moore","source":"The Guardian","date":"14/03/2015","url":"http://www.theguardian.com/society/2015/mar/14/britain-housing-crisis-10-ways-solve-rowan-moore-general-election"},{"headline":"Londoners are paying record rents as growing demand and a supply shortage hit","diptext":"Average rents in the capital exceeded £1,500 for the first time ever in May, according to new data.","author":"Clare Hutchison","source":"Evening Standard","date":"15/06/2015","url":"http://www.standard.co.uk/business/business-news/londoners-are-paying-record-rents-as-growing-demand-and-a-supply-shortage-hit-10320033.html"},{"headline":"London, it’s over, and it’s not me, it’s you","diptext":"At what point do we say enough's enough and actually realise we'd rather live in affordable cities? Starting a new series looking at opportunities beyond the capital, we look into what drives someone out the city of their birth and all the way to Scotland.","author":"Sami Mikhail","source":"Vice","date":"25/06/2015","url":"https://i-d.vice.com/en_gb/article/london-its-over-and-its-not-me-its-you"},{"headline":"London: It's Time for a Rent Strike","diptext":"There are two types of vehicles that strike a particular fear into the heart of London's neighbourhoods: police vans and Foxtons Minis. Synonymous with gentrification, the Foxtons car represents the vanguard of the housing crisis, expanding sky-high rents into fresh territory; \"discovering\" an area before returning to the ubiquitous plasticky offices and their smarmy drinks fridges to market another slice of the city at exorbitant prices.","author":"Ben Beach","source":"Vice","date":"16/06/2015","url":"http://www.vice.com/en_uk/read/rent-strike-ben-beach-839"},{"headline":"Private rents in Britain are the highest in the EU","diptext":"Private rents are higher in Britain than any other European Union country and swallow up more of people’s earnings, according to a new analysis.","author":"Nigel Morris","source":"Evening Standard","date":"24/06/2015","url":"http://www.independent.co.uk/news/uk/home-news/private-rents-in-britain-are-the-highest-in-the-eu-10340664.html"}]



